.services__container{display:flex;flex-direction:column;align-items:center;background-color:#e8e6e6}@media(min-width: 768px){.services__container{padding:60px 16%}}.services__list{display:grid;grid-template-columns:1fr;gap:40px;background-color:#e8e6e6}.services .card{position:relative;display:flex;flex-direction:column;background-color:#dbdbdb;padding:34px;box-sizing:border-box}.services .card .card__img1,.services .card .card__img2,.services .card .card__img3{position:absolute;top:-25px;left:50%;transform:translateX(-50%);width:80px;height:80px;background-size:cover;background-position:center}.services .card__title{font-family:"Geometria Medium";font-size:20px;font-weight:400;line-height:1.4em;letter-spacing:0em;text-align:center;color:#4ca89d;margin-top:50px}.services .card__text{font-family:"Geometria Light";font-size:16px;font-weight:400;line-height:1.6em;text-align:center;color:#000;margin-top:12px}@media(min-width: 768px){.services .card__text{margin-top:20px}}.services .card__footer{display:flex;justify-content:center;margin-top:auto}.services .card__button{display:inline-block;padding:8px 32px;margin-top:24px;width:100%;text-align:center;background-color:#4ca89d;color:#fff;border-radius:8px;text-decoration:none;letter-spacing:0;border:1px solid #4ca89d}.services .card__button:hover{background-color:#fff;color:#4ca89d}.services p strong{font-family:"Geometria Medium"}